import { Divider, Grid, Table, TableBody, TableCell, TableContainer, TableRow, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Product } from "../../app/models/products";
import agent from "../../app/api/agent";
import NotFound from "../../app/errors/NotFound";
import LoadingComponent from "../../app/layout/LoadingComponent";

export default function ProductDetails() {

    const { id } = useParams<{ id: string }>();

    const [product, setProduct] = useState<Product | null>(null);

    const [loading, setLoading] = useState(true);

    useEffect(() => {
       agent.Catalog.details(parseInt(id))
       .then(response => setProduct(response))
       .catch(error => console.log(error))
       .finally(() => setLoading(false))
    }, [id])

    if (loading) return <LoadingComponent message="Ucitavanje proizvoda..."/>
    if (!product) return <NotFound></NotFound>

    return (
        <Grid container spacing={6}>
            <Grid item xs={6}>
                <img src={product.imagePath} alt={product.productName} style={{ width: '100%' }} />
            </Grid>
            <Grid item xs={6}>
                <Typography variant="h3">{product.productName}</Typography>
                <Divider sx={{ mb: 2 }} />
                <Typography variant="h4" color='secondary' >{product.productPrice} RSD</Typography>
                <TableContainer>
                    <Table>
                        <TableBody>
                            <TableRow>
                                <TableCell>Naziv</TableCell>
                                <TableCell>{product.productName}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Dostupnost</TableCell>
                                <TableCell>{product.stockStatus ? "dostupan" : "nije dostupan"}</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </Grid>
        </Grid>
    )

}